<template>
  <div class="header">
    <div style="display:flex">
      <!-- 折叠按钮 -->
      <div class="collapse-btn">
        <img src="https://cloud.fityun.cn/static/image/logo_sm.png" alt="" />
        <div>
          <p>菲特云</p>
          <p>OA系统</p>
        </div>
      </div>
      <div class="tab-top">
        <div
          :class="{
            'tab-on': true,
            active: activename === 'homePage',
            pointer: true,
          }"
          @click="handtab('homePage')"
        >
          <span>首页</span>
        </div>
        <span></span>
        <div
          :class="{
            'tab-on': true,
            active: activename === 'customeManagement',
            pointer: true,
          }"
          @click="handtab('customeManagement')"
        >
          <span>客户管理</span>
        </div>
        <span></span>
        <div
          :class="{
            'tab-on': true,
            active: activename === 'channelExpansion',
            pointer: true,
          }"
          @click="handtab('channelExpansion')"
        >
          <span>渠道扩张</span>
        </div>
        <span></span>
        <div
          :class="{
            'tab-on': true,
            active: activename === 'personalCenter',
            pointer: true,
          }"
          @click="handtab('personalCenter')"
        >
          <span>个人中心</span>
        </div>
      </div>
    </div>
    <div class="header-right" >
      <div class="header-user-con">
        <!-- 消息中心 -->
        <div class="btn-bell">
          <el-tooltip
            effect="dark"
            :content="message ? `有${message}条未读消息` : `消息中心`"
            placement="bottom"
          >
            <router-link to="/tabs">
              <i class="el-icon-bell"></i>
            </router-link>
          </el-tooltip>
          <span class="btn-bell-badge" v-if="message"></span>
        </div>
        <!-- 用户头像 -->
        <div class="user-avator">
          <img src="../assets/img/img.jpg" />
        </div>
        <!-- 用户名下拉菜单 -->
        <el-dropdown class="user-name" trigger="click" @command="handleCommand">
          <span class="el-dropdown-link">
            {{ userinfo.realname }}
            <i class="el-icon-caret-bottom"></i>
          </span>
          <template #dropdown>
            <el-dropdown-menu>
              <a
                href="https://github.com/lin-xin/vue-manage-system"
                target="_blank"
              >
                <el-dropdown-item>项目仓库</el-dropdown-item>
              </a>
              <el-dropdown-item divided command="loginout"
                >退出登录</el-dropdown-item
              >
            </el-dropdown-menu>
          </template>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>
<script>
import { layout } from '../api/manage';
export default {
  data() {
    return {
      fullscreen: false,
      message: 2,
      activename: "homePage",
      userinfo:[],
    };
  },
  created(){
    this.userinfo =JSON.parse(localStorage.getItem("userinfo"));
  },
  computed: {
    collapse() {
      return this.$store.state.collapse;
    },
  },
  methods: {
    // 用户名下拉菜单选择事件
    handleCommand(command) {
      if (command == "loginout") {
        layout(
          {
            module:'1000',
            command:'1002',
            userid:localStorage.getItem("userid"),
          }
        ).then(data=>{
          console.log(data);
          if(data.status == 10000){
            this.$message({
                message: '退出登录',
                type: 'success'
            });
            localStorage.removeItem("userinfo");
            localStorage.removeItem("token");
            localStorage.removeItem("sign");
            localStorage.removeItem("userid");
            this.$router.push("/login");
          }
          
        })
        
      }
    },
    handtab(tab) {
      this.activename = tab;
    },
  },
  mounted() {},
};
</script>
<style scoped>
.header {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  height: 60px;
  font-size: 22px;
  display: flex;
  justify-content: space-between;
  background: #ffffff;
}
.collapse-btn {
  padding: 15px 0;
  height: 60px;
  cursor: pointer;
  box-sizing: border-box;
  line-height: 60px;
  margin: 0 0 0 20px;
  display: flex;
}
.collapse-btn > img {
  width: 46px;
  display: block;
}
.collapse-btn > div {
  width: 52px;
  margin-left: 8px;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
}
.collapse-btn > div > p:first-of-type {
  font-size: 16px;
  font-weight: bold;
  color: #404040;
  line-height: 1;
}
.collapse-btn > div > p:last-of-type {
  font-size: 10px;
  font-weight: bold;
  color: #404040;
  line-height: 1;
  letter-spacing: 1px;
}
.header .logo {
  width: 250px;
  line-height: 60px;
}
.header-right {
  padding-right: 50px;
}
.header-user-con {
  display: flex;
  height: 70px;
  align-items: center;
}
.btn-fullscreen {
  transform: rotate(45deg);
  margin-right: 5px;
  font-size: 24px;
}
.btn-bell,
.btn-fullscreen {
  position: relative;
  width: 30px;
  height: 30px;
  text-align: center;
  border-radius: 15px;
  cursor: pointer;
}
.btn-bell-badge {
  position: absolute;
  right: 0;
  top: -2px;
  width: 8px;
  height: 8px;
  border-radius: 4px;
  background: #f56c6c;
  color: #fff;
}

.user-name {
  margin-left: 10px;
}
.user-avator {
  margin-left: 20px;
}
.user-avator img {
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.el-dropdown-link {
  
  cursor: pointer;
}
.el-dropdown-menu__item {
  text-align: center;
}
.tab-top {
  height: 60px;
  padding-left: 72px;
  display: flex;
  align-items: center;
  line-height: 60px;
}
.tab-top > .tab-on {
  width: 107px;
  height: 60px;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
}
.tab-top > .tab-on > span {
  display: inline-block;
  width: auto;
  height: 60px;
  font-size: 14px;
  font-weight: 400;
  color: #333333;
  line-height: 58px;
  border-bottom: 2px solid rgba(0, 0, 0, 0);
}
.tab-on.active > span {
  color: #2171f4;
  border-bottom: 2px solid #2171f4;
}
.tab-top > span {
  width: 1px;
  height: 20px;
  background: #e1e3e6;
}
.tab-top > .tab-on:hover > span {
  color: #2171f4;
  border-bottom: 2px solid #2171f4;
}
</style>
