<template>
  <div class="addcontract" v-loading="loading">
    <div class="contra-top">
      <div class="contra-on">
        <span class="contra-title">订单状态 <span>*</span></span>
        <el-select
          v-model="contraOrder.status"
          placeholder="请选择"
          size="small"
          style="width: 240px"
        >
          <el-option key="bbk" label="步步高" value="bbk"></el-option>
          <el-option key="xtc" label="小天才" value="xtc"></el-option>
          <el-option key="imoo" label="imoo" value="imoo"></el-option>
        </el-select>
      </div>
      <div class="contra-on">
        <span class="contra-title">成交时间 <span>*</span></span>
        <el-date-picker
          v-model="contraOrder.dealtime"
          size="small"
          style="width: 240px"
          type="date"
          placeholder="选择日期"
        >
        </el-date-picker>
      </div>
      <div class="contra-on">
        <span class="contra-title">合同有效期</span>
        <el-date-picker
          v-model="contraOrder.starttime"
          size="small"
          style="width: 240px"
          type="date"
          placeholder="选择日期"
        >
        </el-date-picker
        >&nbsp;&nbsp;至&nbsp;&nbsp;
        <el-date-picker
          v-model="contraOrder.endtime"
          :disabled="endswich"
          @change="setendtime"
          size="small"
          style="width: 240px"
          type="date"
          placeholder="选择日期"
        >
        </el-date-picker>
      </div>
      <div class="contra-on">
        <span class="contra-title">合同金额 <span>*</span></span>
        <el-input
          type="text"
          v-model="contraOrder.thisPay"
          oninput="value=value.replace(/^\D*(\d*(?:\.\d{0,2})?).*$/g, '$1');if(Number(value)>100000){value=10000};"
          onkeyup="/^\d+(?:\.\d{0,2})?/"
          placeholder="请输入"
          size="small"
          style="width: 240px"
        ></el-input
        ><span style="margin-left: 6px">元</span>
      </div>
      <div class="contra-on">
        <span class="contra-title">赠送 <span>*</span></span>
        <div class="contra-time">
          <el-radio v-model="contraOrder.radio" label="1" text-color="#2171F4"
            >无</el-radio
          >
          <el-radio v-model="contraOrder.radio" label="2" text-color="#2171F4"
            >天数</el-radio
          >
          <el-radio v-model="contraOrder.radio" label="3" text-color="#2171F4"
            >月</el-radio
          >
        </div>
        <div class="contra-input">
          <el-input
            type="text"
            v-if="contraOrder.radio !== '1'"
            v-model="contraOrder.freetime"
            placeholder="请输入"
            onkeyup="value=value.replace(/^(0+)|[^\d]+/g,'')"
            size="small"
            style="width: 98px"
          ></el-input>
          <span v-if="contraOrder.radio === '3'">月</span>
          <span v-if="contraOrder.radio === '2'">天</span>
        </div>
      </div>
      <div class="contra-on" style="height: 52px">
        <span class="contra-title" style="margin-bottom: 20px">备注</span>
        <el-input
          type="textarea"
          resize="none"
          placeholder="选填"
          v-model="textarea"
          maxlength="30"
          style="width: 305px"
        >
        </el-input>
      </div>
    </div>
    <div class="contra-bottom">
                  <el-button size="small" style="width: 67px">取消</el-button>
          <el-button
                    size="small"
                    type="primary"
                    class="submit"
                    style="width: 67px; margin-right: 10px"
                    >提交</el-button
                  >
    </div>
  </div>
</template>
<script>
import { ElMessage } from "element-plus";
export default {
  data() {
    return {
      loading: false,
      contraOrder: {
        status: "",
        dealtime: "",
        starttime: null,
        endtime: "",
        thisPay: "",
        radio: "1",
        freetime: "",
      },
      endswich: true,
    };
  },
  watch: {
    "contraOrder.starttime": {
      handler: function () {
        if (this.contraOrder.starttime) {
          this.endswich = false;
        } else {
          this.contraOrder.endtime = "";
          this.endswich = true;
        }
      },
    },
  },
  methods: {
    setendtime() {
      let start = this.contraOrder.starttime.getTime();
      let end = this.contraOrder.endtime.getTime();
      if (end < start) {
        ElMessage.error({
          message: "结束日期不能小于开始日期",
          duration: 1500,
        });
        this.contraOrder.endtime = "";
      }
    },
  },
};
</script>
<style lang="less">
.addcontract {
  width: 800px;
  .contra-input {
    margin-left: 27px;
    margin-right: 8px;
  }
  .el-radio {
    margin-right: 20px;
  }
  .el-radio__input.is-checked .el-radio__inner {
    background-color: #fff;
    border-color: #2171f4;
  }
  .el-radio__inner::after {
    background-color: #2171f4;
  }
  .contra-top {
    padding: 30px 0 0 40px;
    border-bottom: 1px solid #e9e9e9;
    box-sizing: border-box;
    display: flex;
    flex-flow: column;
    .contra-on {
      height: 32px;
      display: flex;
      align-items: center;
      margin-bottom: 20px;
      span.contra-title {
        width: 105px;
        font-size: 14px;
        font-weight: 400;
        color: #333333;
        line-height: 1;
        span {
          color: #ff3e3e;
        }
      }
      .contra-time {
      }
    }
  }
  .contra-bottom{
    height: 72px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 20px;
    box-sizing: border-box;
    .el-button--primary{
          background-color: #2171F4;
    border-color: #2171F4;
       
    }
    .el-button--primary:hover{
      opacity: .7;
    }
  }
}
</style>
